import * as React from 'react';
import { Routes, Route } from 'react-router-dom';
import Staff from '..';

const GeneralSettingUpdateView = React.lazy(() => import('./GeneralUpdateView'));
const IndexSettingUpdateView = React.lazy(() => import('./IndexUpdateView'));

export default function SettingLayout(props: any) {
  return (
    <Routes>
      <Route path="general/" element={<GeneralSettingUpdateView />} />
      <Route path="index/" element={<IndexSettingUpdateView />} />

      <Route
        path="/"
        element={
          <Staff.View title="Settings" back="staff/" requiredPerms={['miq.view_sitesetting']}>
            <div className="d-grid grid-sm-3 grid-md-4">
              <SettingLink to="general/" label="General" text="Site settings and branding" />
              <SettingLink to="index/" label="Index" text="Homepage settings" />
            </div>
          </Staff.View>
        }
      />
    </Routes>
  );
}

const SettingLink = ({ label, text, ...rest }: any) => {
  return (
    <Staff.Link {...rest} className="d-block">
      <span className="text-md">{label}</span>
      <p className="text-muted text-sm pb-3">{text}</p>
    </Staff.Link>
  );
};
