import * as React from 'react';

import { SharedDataCtx, SharedDataState } from '@miq/contextjs';
import { getClassName } from '@miq/utiljs';
import { Loading } from '@miq/componentjs';

type SharedProps = {
  children: React.ReactNode;
  className?: string;
  id?: string;
};

//#region StaffLayout

export type StaffLayoutProps = SharedProps & {
  fallback?: React.ReactNode | string;
};

export default function Layout({ children, fallback }: StaffLayoutProps): JSX.Element | null {
  const ctx: SharedDataState = React.useContext(SharedDataCtx);

  if (!ctx) return null;
  if (!ctx.isLoaded) return <Loading />;

  const { user } = ctx;
  if (!user || !user.is_staff) return fallback || <h1>Not allowed</h1>;

  return <div className="miq-staff-layout">{children}</div>;
}

//#endregion StaffLayout

//#region Sidebar

export type StaffSidebarProps = SharedProps & {
  header?: React.ReactNode | string;
  footer?: React.ReactNode | string;
};

export const Sidebar = ({ children, header, footer, ...props }: StaffSidebarProps) => {
  return (
    <nav className={getClassName(['miq-staff-layout-sidebar', props.className])}>
      {header && <header className="miq-staff-layout-sidebar-header">{header}</header>}

      <section className="miq-staff-layout-sidebar-body">
        <div className="miq-staff-layout-sidebar-body-content">{children}</div>

        {footer && <div className="miq-staff-layout-sidebar-body-footer">{footer}</div>}
      </section>
    </nav>
  );
};

//#endregion Sidebar

//#region Main

export type StaffMainProps = SharedProps & {};

export const Main = ({ children, className, ...props }: StaffMainProps) => {
  return (
    <div {...props} className={getClassName(['miq-staff-layout-main', className])} role="main">
      <div className="miq-container-fluid">{children}</div>
    </div>
  );
};

//#endregion Main

//#region

export type StaffMobileNavProps = SharedProps & { children?: React.ReactNode };

export const MobileNav = ({ children, ...props }: StaffMobileNavProps) => {
  return <nav className={getClassName(['miq-staff-mobile-nav', props.className])}>{children}</nav>;
};
//#endregion
