import React from 'react';
import { withHasPerms } from './utils';
import { getClassName as getCN } from '@miq/utiljs';

export function ViewSection({ children, ...props }: any) {
  const { footer, border } = props;

  return (
    <div className={getCN(['miq-view-section', border && 'border', props.className])}>
      <Header {...props} />
      <div className="miq-view-section-body">{children}</div>
      {footer && <div className="miq-view-section-footer">{footer}</div>}
    </div>
  );
}

export const StaffViewSection = withHasPerms(ViewSection);

//#region Header

const Header = (props: any) => {
  const { header, title, text, actions } = props;
  const hasHeader = header || title || text || actions;

  if (!hasHeader) return null;
  if (header) return <div className={getCN(['miq-view-section-header', props.headerClassName])}>{header}</div>;

  return (
    <div className={getCN(['miq-view-section-header', props.headerClassName])}>
      <div className="d-flex align-items-center justify-content-between">
        <div className="miq-view-section-header-title">{title}</div>

        {actions && <section className="miq-view-section-header-actions">{actions}</section>}
      </div>

      {text && <div className="miq-view-section-header-text">{text}</div>}
    </div>
  );
};

//#endregion Header
