import * as React from 'react';
import { Link, NavLink } from 'react-router-dom';
import './staff.scss';

import { SharedDataCtx, SharedDataState } from '@miq/contextjs';
import { Button } from '@miq/componentjs';

import Layout, { Main, MobileNav, Sidebar } from './Layout';
import StaffView from './Views';
import SettingLayout from './Setting';
import { withHasPerms } from './utils';
import { StaffViewSection } from './ViewSection';
import AccountStaffLayout from './Account';
import StaffImg from './Img';

export type StaffType = {};

export default abstract class Staff {
  public static Layout = Layout;

  public static AccountNavLink = (props: any) => {
    const { user } = React.useContext<SharedDataState | any>(SharedDataCtx);
    return (
      <Staff.NavLink {...props} to="account/">
        {user.first_name}
      </Staff.NavLink>
    );
  };

  public static SettingsNavLink = (props: any) => (
    <Staff.NavLink {...props} to="settings/">
      Settings
    </Staff.NavLink>
  );
  public static SettingLayout = SettingLayout;
  public static AccountLayout = AccountStaffLayout;
  public static Sidebar = Sidebar;
  public static MobileNav = MobileNav;
  public static Main = Main;

  public static View = StaffView;

  public static Button = withHasPerms(Button, { className: 'miq-staff-btn' });
  public static Link = withHasPerms(Link, { className: 'miq-staff-link' });
  public static NavLink = withHasPerms(NavLink, { className: 'miq-staff-nav-link' });
  public static Section = StaffViewSection;
  public static Img = StaffImg;
}

