import { createContext, useState, useMemo } from 'react';

import { IS_DEV } from '@miq/utiljs';

export const FormCtx = createContext(null);

export const FormProvider = FormCtx.Provider;

export default FormCtx;

export const useForm = (defaultValues = {}) => {
  const [values, setValues] = useState({ ...defaultValues });
  const [errors, setErrors] = useState({});

  const context = useMemo(() => {
    const handleError = (err, log = true) => {
      if (!err || !err.response) {
        return;
      }

      const { response } = err;
      if (response.status !== 400) {
        IS_DEV && log && console.error(`Request error status: ${response.status}`);
        return err;
      }

      let newData = {};
      const { data } = response;

      if (IS_DEV && log) {
        console.error('ERROR=>', data);
      }

      Object.keys(data).forEach((key) => {
        let e = data[key];
        if (Array.isArray(e) && e.length > 0) {
          e = e[0];
        }
        newData = { ...newData, [key]: e.message || e };
      });

      setErrors({ ...newData });
    };

    const setError = (key, value) => {
      setErrors({ ...errors, [key]: value });
    };

    const handleChange = (e) => {
      const { name } = e.target;

      if (Object.keys(errors).includes(name)) {
        setErrors({ ...errors, [name]: null });
      }

      switch (e.target.type) {
        case 'checkbox':
          return toggleCheck(e);

        default:
          return setValue(e.target.name, e.target.value);
      }
    };

    const setValue = (key, value) => {
      setValues({ ...values, [key]: value });
    };

    const toggleCheck = (e) => {
      const { name, checked } = e.target;
      return setValue(name, checked);
    };

    return {
      values,
      setValue,
      setValues,
      handleChange,
      toggleCheck,
      errors,
      setError,
      setErrors,
      handleError,
      hasErrors: () => Object.keys(errors).join() !== '',
    };
  }, [values, errors]);

  return context;
};
