import * as React from 'react';

import { Link } from 'react-router-dom';
import { getClassName as getCN } from '@miq/utiljs';
import { withHasPerms } from './utils';

//#region StaffView

type HeaderProps = {
  text?: string;
  back?: string;
  title?: string | React.ReactNode;
  header?: React.ReactNode;
  actions?: React.ReactNode;
};

type ViewProps = HeaderProps & {
  loading?: boolean | string;
  children?: any;
  footer?: React.ReactNode;
  className?: string;
  id?: string;
};

function StaffView({ children, ...props }: ViewProps) {
  const { header, title, text, actions, back, footer, loading, ...rest } = props;
  return (
    <div {...rest} className={getCN(['miq-staff-view', props.className])}>
      <Header {...{ header, title, text, actions, back }} />

      <div className="miq-staff-view-main">{children}</div>

      {footer && <div className="miq-staff-view-footer">{footer}</div>}
    </div>
  );
}

export default withHasPerms(StaffView);

//#endregion StaffView

//#region Header

const Header = ({ header, title, text, actions, back }: HeaderProps) => {
  const hasHeader = header || title || text || actions || back;
  if (!hasHeader) return null;
  if (header) return <div className="miq-staff-view-header-wrapper">{header}</div>;
  return (
    <header className="miq-staff-view-header-wrapper">
      <div className="miq-staff-view-header d-flex">
        <div className="d-flex align-items-center">
          {back && (
            <Link to={back} className="btn miq-staff-view-header-back me-1" title="Go back">
              {`<`}
            </Link>
          )}

          <div className="miq-staff-view-header-title">
            {typeof title === 'string' ? <div className="miq-staff-view-header-title-text">{title}</div> : title}
          </div>
        </div>

        {actions && <section className="miq-staff-view-header-actions">{actions}</section>}
      </div>

      {text && <div className="miq-staff-view-header-text">{text}</div>}
    </header>
  );
};

//#endregion Header
