import React, { forwardRef, useContext } from 'react';

import { Icons } from '@miq/componentjs';
import { getClassName, isRequired } from '@miq/utiljs';
import FormCtx from './context';

const withFormTextInput = (Component, args = {}) =>
  forwardRef((props, ref) => {
    const ctx = useContext(FormCtx) || isRequired('FormCtx');
    let { name, error, ...rest } = props;

    const onChange = (e) => {
      ctx.handleChange(e);
    };

    error = error || ctx.errors[name];
    const className = getClassName([args.className, props.className, error && 'is-invalid']);

    return (
      <Component
        value={ctx.values[name]}
        onChange={onChange}
        {...args.input}
        {...rest}
        name={name}
        className={className}
        ref={ref}
      />
    );
  });

export const FormTextInput = withFormTextInput('input', { className: 'miq-form-input' });
export const FormTextArea = withFormTextInput('textarea', { className: 'miq-form-textarea' });

export const SearchInput = forwardRef((props, ref) => {
  const { id, className, inputClassName, ...rest } = props;
  return (
    <div {...{ id }} className={getClassName(['miq-search-input-wrapper d-flex align-items-center', className])}>
      <div className="icon-wrapper">
        <Icons.Search className="miq-search-icon" />
      </div>

      <input
        name="q"
        placeholder="Search ..."
        minLength={3}
        maxLength={99}
        className={getClassName(['miq-form-input miq-search-input', inputClassName])}
        {...rest}
        type="search"
        ref={ref}
      />
    </div>
  );
});

//#region PhoneInput

/**
 * Phone input
 * Usage:
 * ```js
 * <PhoneInput onchange={(e)=>setState(e.target.value)} minLength={7} maxLength={16} pattern="[0-9]*"/>
 * ```
 *
 * @typedef {Object} PhoneProps
 * @property {string} pattern
 *
 * @typedef {InnerProps & PhoneProps} PhoneInputProps
 *
 * @param {PhoneInputProps} props
 *
 * @augments {Component<Props, State>}
 */

export const FormPhoneInput = withFormTextInput('input', {
  className: 'miq-form-input',
  input: {
    minLength: 7,
    maxLength: 16,
    pattern: '[0-9]*',
    type: 'tel',
    onKeyPress: (e) => !/[0-9]/.test(e.key) && e.preventDefault(),
  },
});

//#endregion PhoneInput

export const TextAreaX = forwardRef((props, ref) => {
  const { id, clearFocus, value, onChange, placeholder = 'Start typing ...', ...rest } = props;
  const style = props.style;

  return (
    <div id={id} className={getClassName(['miq-textareax', clearFocus && 'clear-focus'])}>
      <span className="miq-textareax-input-mirror" {...{ style }}>{`${value}\n`}</span>

      <div className="miq-textareax-input-wrapper">
        <textarea {...rest} {...{ value, onChange, placeholder, style }} ref={ref} />
      </div>
    </div>
  );
});

export const FormTextAreaX = withFormTextInput(TextAreaX);

//#region SelectInput

export const FormSelectInput = forwardRef(({ children, ...props }, ref) => {
  const ctx = useContext(FormCtx) || isRequired('FormCtx');
  let { name = isRequired('name prop'), error, nullValue, ...rest } = props;

  error = error || ctx?.errors?.[name];

  return (
    <select
      value={ctx?.values?.[name]}
      onChange={ctx.handleChange}
      {...rest}
      name={name}
      className={getClassName(['miq-form-select', error && 'is-invalid', rest.className])}
      ref={ref}
    >
      {nullValue?.label && <Option {...nullValue} value={nullValue?.value || ''} />}
      {children}
    </select>
  );
});

type OptionProps = {
  value: string | number;
  label?: string|any;
  children?: React.ReactNode;
};

export const Option = ({ label, children, ...props }: OptionProps) => (
  <option {...props} value={props.value}>
    {children || label}
  </option>
);

//#endregion SelectInput

//#region CheckboxInput

export const FormCheckboxInput = forwardRef((props, ref) => {
  const ctx = useContext(FormCtx) || isRequired('FormCtx');
  let { name = isRequired('Name prop'), error } = props;

  error = error || ctx?.errors?.[name];

  return (
    <input
      onChange={ctx.toggleCheck}
      checked={ctx.values[name]}
      {...props}
      type="checkbox"
      className={getClassName(['miq-form-checkbox', error && 'is-invalid', props.className])}
      ref={ref}
    />
  );
});

//#endregion CheckboxInput
