import React, { createRef, forwardRef } from 'react';

import Form from '@miq/formjs';
import { isString, isRequired as isR, getClassName as getCN } from '@miq/utiljs';
import { AbstractCrudStaffImgModel, FileInput, Button, Img } from '@miq/componentjs';

//#region StaffImg

export default class StaffImg extends AbstractCrudStaffImgModel {
  static postFromUrl(url = isR('url'), alt_text = '') {
    return super.post({ src: url, alt_text });
  }

  static post(file = isR('file'), alt_text = '') {
    if (file.name.length > 499) {
      return Promise.reject('Filename too long');
    }

    const fd = new FormData();
    fd.append('src', file, file.name);
    fd.append('alt_text', alt_text);

    return super.post(fd, { headers: { 'content-type': 'multipart/form-data' } });
  }

  patchMobile(file) {
    return this._patch(file, null, 'mobile');
  }
  patchSquare(file) {
    return this._patch(file, null, 'square');
  }
  patchThumb(file) {
    return this._patch(file, null, 'thumb');
  }
  patch(file) {
    return this._patch(file);
  }
  patchAlt(alt_text = '') {
    return super.patch({ alt_text }, { alt_text: this.alt_text });
  }

  _patch(file = isR('file'), alt_text, field) {
    if (file.name.length > 499) {
      return Promise.reject('Filename too long');
    }

    const fd = new FormData();

    if (!isString(field)) field = 'src';
    switch (field) {
      case 'mobile':
        fd.append('src_mobile', file, file.name);
        break;
      case 'thumb':
        fd.append('thumb', file, file.name);
        break;
      case 'square':
        fd.append('thumb_sq', file, file.name);
        break;
      default:
        fd.append('src', file, file.name);
        break;
    }

    if (alt_text) fd.append('alt_text', alt_text);

    return super.patch(fd, null, { headers: { 'content-type': 'multipart/form-data' } });
  }

  static get Fields() {
    return Object.freeze({
      ...Img.Fields,
      slug: 'slug',
      name: 'name',
      name_truncated: 'name_truncated',
      size_mobile: 'size_mobile',
      created: 'created',
      updated: 'updated',
      position: 'position',
    });
  }
}

StaffImg.prototype._endpoint = 'staffimages/';

//#endregion StaffImg

//#region COMPONENTS

const ACCEPT = 'image/*';
const MAX_UPLOAD_COUNT = 10;

const AddButton = forwardRef(({ children, ...props }, ref) => {
  let uploadRef = createRef();

  const { label, multiple, accept = ACCEPT, ...func } = props;
  const { onUpload, onSuccess, onSuccessMulti, onError, onErrorMulti, ...rest } = func;

  const uploadFile = (e) => {
    if (onUpload) return onUpload(e);

    const file = Array.from(e.target.files)[0];
    return StaffImg.post(file)
      .then((res) => onSuccess?.(res))
      .catch((err) => onError?.(err));
  };

  const uploadFiles = (e) => {
    if (onUpload) return onUpload(e);

    const promises = [];
    const errors = [];

    const files = Array.from(e.target.files).splice(0, MAX_UPLOAD_COUNT);
    files.forEach((file, i) => {
      promises.push(StaffImg.post(file).catch((err) => errors.push(err)));
    });

    if (errors && onErrorMulti) onErrorMulti(errors);

    Promise.all(promises).then((responses) => {
      if (onSuccessMulti) return onSuccessMulti(responses);
    });
  };

  return (
    <>
      <Button className="miq-img-create-btn" {...rest} onClick={() => uploadRef.click()} ref={ref}>
        {children || label || `Upload image${multiple ? 's' : ''}`}
      </Button>
      <FileInput
        accept={accept}
        multiple={multiple}
        onChange={multiple ? uploadFiles : uploadFile}
        ref={(file) => (uploadRef = file)}
      />
    </>
  );
});

const UpdateButton = forwardRef((props, ref) => {
  let uploadRef = createRef();
  const { instance = isR('Image instance'), accept = ACCEPT, ...func } = props;
  let { field = 'src', onUpload, onSuccess, onError, ...rest } = func;

  const uploadFile = (e) => {
    if (field === 'picture') return null;
    if (onUpload) return onUpload(e);

    const file = Array.from(e.target.files)[0];
    return instance
      ._patch(file, null, field)
      .then((res) => onSuccess?.(res))
      .catch((err) => onError?.(err));
  };

  return (
    <>
      <Button className="miq-img-update-btn" {...rest} onClick={() => uploadRef.click()} ref={ref} />
      <FileInput accept={accept} onChange={uploadFile} ref={(file) => (uploadRef = file)} />
    </>
  );
});

const DeleteButton = forwardRef(({ children, ...props }, ref) => {
  const { instance = isR('Image instance'), label, ...func } = props;
  const { onSuccess, onError, ...rest } = func;

  const handleDelete = (e) =>
    instance
      .delete()
      .then((res) => onSuccess?.(res))
      .catch((err) => onError?.(err));
  return (
    <Button {...rest} onClick={handleDelete} className={getCN(['miq-img-delete-btn', props.className])} ref={ref}>
      {children || label || `Delete Image`}
    </Button>
  );
});

//#endregion COMPONENTS

//#region EXPORTS
StaffImg.AddButton = AddButton;
StaffImg.UpdateButton = UpdateButton;
StaffImg.DeleteButton = DeleteButton;
StaffImg.ViewSelect = (props) => (
  <Form.Select name="view" {...props}>
    <Form.Option value="src" label="Desktop" />
    <Form.Option value="mobile" label="Mobile" />
    <Form.Option value="thumb" label="Thumbnail" />
    <Form.Option value="square" label="Square" />
    <Form.Option value="picture" label="Picture" />
  </Form.Select>
);

//#endregion EXPORTS
